html, body {
  margin: 0;
  height: 100%;
}
#c {
  width: 100vw;
  height: 100vh;
  display: block;
}
#info {
  font: 15px Arial, sans-serif;
  font-size: 40px;
  text-shadow: -1px -1px 0 rgb(230, 72, 72), 1px -1px 0 rgb(74, 197, 50), -1px 1px 0 rgb(126, 118, 238), 1px 1px 0 #fff;
  color: rgb(107, 26, 26);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
	z-index: 100;
	display:block;
}
  